body {
  margin: 0;
  font-family: Arial, sans-serif;
  background-color: #f0f0f0;
  color: #333;
}

.App-header {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #282c34;
  padding: 20px;
  color: white;
  text-align: left;
}

.profile-pic {
  --s: 15px;
  --b: 2px;
  --c: #7b3b3b;
  --w: 120px;
  --h: 150px;

  width: var(--w);
  height: var(--h);
  border-radius: 20%;
  object-fit: cover;
  padding: calc(2 * var(--s));
  --_g: var(--c) var(--b), #0000 0 calc(100% - var(--b)), var(--c) 0;
  background: linear-gradient(var(--_g)) 50%/100% var(--_i, 100%) no-repeat,
    linear-gradient(90deg, var(--_g)) 50% / var(--_i, 100%) 100% no-repeat;
  outline: calc(var(--w) / 2) solid #0009;
  outline-offset: calc(var(--w) / -2 - 2 * var(--s));
  transition: 0.4s;
  cursor: pointer;
  margin-right: 50px;
}

.profile-pic:hover {
  outline: var(--b) solid var(--c);
  outline-offset: calc(var(--s) / -2);
  --_i: calc(100% - 2 * var(--s));
}

.header-text {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

nav {
  margin-top: 10px;
}

nav a {
  margin: 0 15px;
  color: #61dafb;
  text-decoration: none;
}

nav a:hover {
  text-decoration: underline;
}

.about-section,
.projects-section,
.skills-section,
.contact-section {
  padding: 40px 20px;
  background-color: #fff;
  margin: 20px;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

h2 {
  color: #333;
}

ul {
  list-style-type: none;
  padding: 0;
}

.project {
  margin-bottom: 20px;
}

.project h3 {
  margin-bottom: 5px;
}

.project p {
  margin: 0;
  color: #555;
}

.project-section {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  margin-top: 20px;
}

.project-link-image {
  width: 100px;
  height: auto;
  border-radius: 10px;
  margin-right: 20px;
  transition: transform 0.3s ease;
}

.project-link-image:hover {
  transform: scale(1.1);
}

.contact-section a {
  color: #61dafb;
  text-decoration: none;
}

.contact-section a:hover {
  text-decoration: underline;
}

.skills-section {
  padding: 20px;
  text-align: center;
}

.skills-list {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}

.skill-item {
  margin: 20px;
  text-align: center;
}

.skill-item img {
  width: 80px;
  height: 80px;
  margin-bottom: 10px;
}

.skill-item p {
  font-size: 1rem;
  color: #333;
}

.timeline-section {
  padding: 50px 0;
  text-align: center;
}

.timeline {
  position: relative;
  margin: 20px 0;
  padding: 0 40px;
}

.timeline::before {
  content: "";
  position: absolute;
  left: 50%;
  top: 0;
  bottom: 0;
  width: 2px;
  background: #ddd;
  transform: translateX(-50%);
}

.timeline-item {
  position: relative;
  margin: 40px 0; /* Consistent margin */
}

.timeline-item::before {
  content: "";
  position: absolute;
  left: 50%;
  top: 0;
  transform: translateX(-50%);
  width: 12px;
  height: 12px;
  background-color: #333;
  border-radius: 50%;
  z-index: 1;
}

.timeline-content {
  background-color: #f9f9f9;
  padding: 10px 20px;
  border-radius: 6px;
  display: inline-block;
  width: 300px; /* Consistent width */
  text-align: left;
  position: relative;
}

.timeline-content h3 {
  font-size: 1.2rem;
  margin: 0 0 10px;
}

.timeline-content p {
  font-size: 1rem;
  color: #666;
}

.timeline-item.left .timeline-content {
  left: 0;
  transform: translateX(-110%); /* Adjust if necessary */
  text-align: right;
}

.timeline-item.right .timeline-content {
  right: 0;
  transform: translateX(110%); /* Adjust if necessary */
  text-align: left;
}
